.mine_account_page .pool_detail{
    padding: 2rem 0;
}
.mine_account_page .pool_text_color{
    color: #fff;
    font-size: 1.3rem;
    text-shadow: 0 0 18px #ff0473,0 0px 18px #ffffff;
}
.mine_account_page .pool_list_ele{
    margin-top: 1rem;
}
.mine_account_page .pool_list_ele p{
    font-size: 0.8rem;
    color: #00c9f3;
    text-align: right;
    padding-right: 1.5rem;
    margin-top: 0.3rem;
}
.mine_account_page .pool_eth_icon{
    height: 1.5rem;
    margin-left: 0.5rem;

}
.mine_account_page .pool_label{
    font-size: 1.1rem;
    color: #00c9f3;
}
.mine_account_page .last-l{
    border-top: 1px solid #156086;
    padding-top: 1rem;
}
.mine_account_page .get-wallet{
    width: 100%;
    background-image: -webkit-linear-gradient(0deg, rgba(8,175,251,0.8),rgba(94,67,252,0.8), rgba(249,93,184,0.8));
    text-align: center;
    font-size: 1rem;
    padding: 0.75rem 0;
    border-radius: 0.5rem;
    color: #16f7ff;
}


