.recharge_content {
    min-height: 100%;
    padding: 1rem;
    background-image: url('../../image/mine/mine_bg.png');
    background-size: 100% 100%;
    color: #ffffff;
    background-attachment: fixed;
}
.recharge_page .list_ele{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    margin-top: 1.5rem;
}
.recharge_page .list_ele label{
    font-size: 1.1rem;
}
.recharge_page .list_ele input{
    margin-top: 0.5rem;
    background-color: rgba(206, 235, 255,0.15);
    padding: 0.5rem;
    font-size: 1rem;
    border: none;
    border-radius: 0.3rem;
    color: #ffffff;
}
.recharge_page .auth_code_content{
    background-color: rgba(206, 235, 255,0.15);
    margin-top: 0.5rem;
    border-radius: 0.3rem;
    padding: 0 0.5rem;
}
.recharge_page .auth_code_content input{
    background-color: transparent;
    margin-top: 0;
}
.recharge_page .get_auth_code_btn{
   color: #fb59af;
}
.recharge_page .submit_btn{
    padding: 0.75rem;
    width: 100%;
    text-align: center;
    color: #ffffff;
    margin-top: 2rem;
    border-radius: 0.3rem;
    font-size: 1rem;
}
.recharge_page .upload_img_content{
    position: relative;
    margin-top: 1rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}
.recharge_page .zfb_img{
    position:absolute;
    top: 0;
    left: 20%;
    opacity: 0;
    width: 60%;
    min-height: 12rem;
}
.recharge_page .camera{
    width: 60%;
    min-height: 12rem;
    margin-bottom: 0 auto;
    /* background-color: red; */
}


