.current_game_page .group_content_2_timer_content{
    position: relative;
    font-size: 1rem;
    padding: 1.5rem 0.75rem 0;
}
.current_game_page .group_content_2_timer_question_blue{
    position: absolute;
    right: 0;
    height: 1rem;
    margin-top: 0.2rem;
}
.current_game_page .group_content_2_timer_time span{
    font-size: 3rem;
    color: #ffffff;
    text-shadow: 0 0 18px #ff0473,0 0px 18px #ffffff
}
.current_game_page .group_content_2_progress_bar{
    width: 100%;
    margin-top: 0.5rem;
    background-image: url('../../image/progress_bar.png');
    background-position: 100%;
    height: 10px;
}
.current_game_page .prigress_point{
    display: block;
    height: 100%;
    width: 100%;
}
.current_game_page .pool_text_color{
    color: #fff;
    font-size: 1.3rem;
    text-shadow: 0 0 18px #ff0473,0 0px 18px #ffffff;
}
.current_game_page .pool_list_ele{
    margin-top: 1rem;
}
.current_game_page .pool_list_ele p{
    /* font-size: 0.9rem; */
    font-size: 0.8rem;
    color: #00c9f3;
    text-align: right;
    padding-right: 1.5rem;
    margin-top: 0.3rem;
}
.current_game_page .pool_eth_icon{
    height: 1.5rem;
}
.current_game_page .pool_label{
    /* font-size: 1.3rem; */
    font-size: 1.1rem;
    color: #00c9f3;
}
.airload_content p{
    color: #000000;
}


