
.business_school_content{
    background-image: url('../../image/wallet/bg-wallet.png');
    background-size: 100% 100%;
}
.business_school_page .wallet_record_main *{
    color: #babcc1;
}
.business_school_page .font-size-20{
    font-size: 1rem;
}
.business_school_page .font-size-14{
    font-size: 0.8rem;
}
.business_school_page .text-color-pink{
    color: #ff0186 !important;
}
.business_school_page .text-color-blue{
    color: #00aadb !important;
}
.business_school_page .text-color-white{
    color: #ffffff !important;
}
.business_school_page .business_school_page_content{
    /* min-height: 100%;
    background-image: url('../../image/wallet/bg-wallet.png');
    background-size: 100% 100%; */
    padding-top: 3rem;
}
.business_school_page .business_school_page_content_header_content{
    padding: 0.2rem;
    background-color: rgba(206, 235, 255,0.15);
    background-color: #050c19;
    position: fixed;
    top: 3rem;
    left: 0;
    width: 100%;
}
.business_school_page .business_school_page_content_header{
    font-size: 1rem;
    padding: 0.75rem 0;
    width: 100%;
    position: relative;
    display: -ms-flexbox;
    display: flex;
}
.business_school_page .business_school_page_content_header::after{
    content: '';
    height: 3px;
    width: 90%;
    background-color: #000000;
    position: absolute;
    left: 5%;
    bottom: 0;
    border-radius: 3px !important;
}
.business_school_page .business_school_page_content_header div{
    position: relative;
    height: 100%;
    width: calc(100% / 3);
    text-align: center;
    color: #00a1bb;
}
.business_school_page .business_school_page_content_header div span{
    position:relative;
}
.business_school_page .business_school_page_content_header  div span::before{
    content: '';
    height: 2px;
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 0;
}
.business_school_page .business_school_page_content_header_active{
    color: #00ffef !important;
}
.business_school_page .business_school_page_content_header_active::before{
    background-image: url('../../image/header_active.png');
    background-size: 100% 100%;
    z-index: 9;
    bottom: calc(-0.75rem - 10px) !important;
    height: 10px !important;
}
.business_school_page .list_ele{
    width: calc(100% - 3rem);
    margin: 1rem auto;
    padding: 0.75rem;
    border-radius: 0.5rem;
    background-color: rgba(218, 246, 255, 0.15);
}
.business_school_page .list_ele img{
    width: 5rem;
}
.business_school_page .list_ele p{
    color: #00f6ff !important;
    font-size: 1rem;
    padding-right: 0.75rem;
}
.business_school_page .list_ele_content{
    width: 60%;
    /* padding: 0.75rem; */
    color: #00f6ff !important;
    font-size: 1rem;
    padding-right: 0.75rem;
    /* padding-left: 0; */
}
.business_school_page .img_content{
    width: 40%;
    /* padding: 0.75rem; */
}
.business_school_page .list_ele_img{
    width: 100%;
}
