
.wallet_record_content{
    background-image: url('../../image/transition/transition_bg.png');
    background-size: 100% 100%;
}
.wallet_record_page .wallet_record_main *{
    color: #babcc1;
}
.wallet_record_page .font-size-20{
    font-size: 1rem;
}
.wallet_record_page .font-size-14{
    font-size: 0.8rem;
}
.wallet_record_page .text-color-pink{
    color: #ff0186 !important;
}
.wallet_record_page .text-color-blue{
    color: #00aadb !important;
}
.wallet_record_page .text-color-white{
    color: #ffffff !important;
}
.wallet_record_page .wallet_record_page_content{
    height: 100%;
    background-image: url('../../image/wallet/bg-wallet.png');
    background-size: 100% 100%;
}
.wallet_record_page .wallet_record_page_content_header_content{
    padding: 0.2rem;
    background-color: rgba(206, 235, 255,0.15)
}
.wallet_record_page .wallet_record_page_content_header{
    font-size: 1rem;
    padding: 0.75rem 0;
    width: 100%;
    position: relative;
    display: -ms-flexbox;
    display: flex;
}
.wallet_record_page .wallet_record_page_content_header::after{
    content: '';
    height: 3px;
    width: 90%;
    background-color: #000000;
    position: absolute;
    left: 5%;
    bottom: 0;
    border-radius: 3px !important;
}
.wallet_record_page .wallet_record_page_content_header div{
    position: relative;
    height: 100%;
    width: calc(100% / 3);
    text-align: center;
    color: #00a1bb;
}
.wallet_record_page .wallet_record_page_content_header div span{
    position:relative;
}
.wallet_record_page .wallet_record_page_content_header  div span::before{
    content: '';
    height: 2px;
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 0;
}
.wallet_record_page .wallet_record_page_content_header_active{
    /* position: relative; */
    color: #00ffef !important;
}
.wallet_record_page .wallet_record_page_content_header_active::before{
    background-image: url('../../image/header_active.png');
    background-size: 100% 100%;
    z-index: 9;
    bottom: calc(-0.75rem - 10px) !important;
    height: 10px !important;
}
.wallet_record_page .wallet_record_page_list_content{
    padding: 0 1rem;
    padding-bottom: 1rem;
}
.wallet_record_page .list_ele{
    padding: 0 0.5rem;
    background-color: rgba(206, 235, 255,0.15);
    border-bottom-left-radius: 0.3rem;
    border-bottom-right-radius: 0.3rem;
    margin-top: 1rem;
}
.wallet_record_page .list_ele>div{
    padding: 0.3rem 0;
}
.wallet_record_page .list_ele>div:nth-of-type(1){
    border-bottom: 1px dashed #597ab0;
}
.wallet_record_page .list_ele>div:nth-of-type(2){
    margin-top: 0.3rem;
}
.wallet_record_page .list_ele_price_label{
    min-width: 30%;
    width: 30%;
}
.wallet_record_page .list_ele_address_label{
    width: 70%;
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    white-space: nowrap;
}


