.invite_player_page .invite-player{
    padding: 1rem 0.75rem;
    text-align: center;
}
.invite_player_page .invite-player .title{
    font-size: 0.8rem;
    text-align: center;
    margin-top: 1rem;
}
.invite_player_page .invite-player .address-wrap ,.invite_player_page .invite-player .btn-save{
    padding: 0.5rem 1rem;
    font-size: 0.8rem;
    text-align: center;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
     width: 85%;
     margin:0 auto;
     background:#272b3a;
     border-radius: 0.25rem;
     margin-top: 1rem;
}
.invite_player_page .invite-player .btn-copy{
    text-align: center;
    background:#272b3a;
     padding: 0.5rem 0.75rem;;
     border-radius: 0.25rem;
     font-size: 0.9rem;
     display: inline-block;
     margin: 0 auto;
    margin-top: 1rem;
}  
.invite_player_page .invite-player .qr-code{
 width: 62%;
 min-width: 182px;
 max-width: 182px;
 margin: 0 auto;
 -webkit-box-shadow: 0 0 1rem #07e0f3 inset;
         box-shadow: 0 0 1rem #07e0f3 inset;
 padding: 1rem;
 padding-top: 1.1rem;
 border-radius: 0.5rem;
 margin-top: 1rem;
}
.invite_player_page .invite-player .qr-code img{
    width: 100%;
}
.invite_player_page .invite-player .btn-save{
    font-size: 0.9rem;
    background: #292d3cad
}


