.invest_count_page .invest_count{
    padding:1rem 0;
}
.invest_count_page .invest_count .pool_text_color{
    color: #fff;
    font-size: 1.3rem;
    text-shadow: 0 0 18px #ff0473,0 0px 18px #ffffff;
}
.invest_count_page .invest_count .pool_list_ele{
    margin-top: 1rem;
}
.invest_count_page .invest_count .pool_list_ele p{
    font-size: 0.7rem;
    color: #00c9f3;
    text-align: right;
    padding-right: 1.5rem;
    margin-top: 0.3rem;
}
.invest_count_page .invest_count .pool_eth_icon{
    height: 1.5rem;
}
.invest_count_page .invest_count .pool_label{
    font-size: 1rem;
    color: #00c9f3;
}
.invest_count_page .invest_count .last-l{
    border-bottom: 1px solid #156086;
    padding: 0.75rem 0;
}
.invest_count_page .get-wallet{
    width: 100%;
    background-image: -webkit-linear-gradient(0deg, rgba(8,175,251,0.8),rgba(94,67,252,0.8), rgba(249,93,184,0.8));
    text-align: center;
    font-size: 1rem;
    padding: 0.75rem 0;
    border-radius: 0.5rem;
    color: #16f7ff;
}
.invest_count_page .invest_count .title{
    font-size: 1.1rem;
    text-align: center;
    padding-top: 2rem;
    border-bottom: 1px solid #156086;
    padding-bottom: 1.5rem;
}
.invest_count_page .invest_count .left-img{
    width: 3rem;;
}
.invest_count_page .invest_count .right-wrap{
    width: 100%;
    padding-left: 0.5rem;
}
.invest_count_page .pool_list_ele_time{
    white-space: nowrap;
}


