.forget_password_content {
    min-height: 100%;
    padding: 1rem;
    background-image: url('../../image/mine/mine_bg.png');
    background-size: 100% 100%;
    color: #ffffff;
    background-attachment: fixed;
}
.forget_password_page .list_ele{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    margin-top: 1.5rem;
}
.forget_password_page .list_ele label{
    font-size: 1.1rem;
}
.forget_password_page .list_ele input{
    margin-top: 0.5rem;
    background-color: rgba(206, 235, 255,0.15);
    padding: 0.5rem;
    font-size: 1rem;
    border: none;
    border-radius: 0.3rem;
    color: #ffffff;
}
.forget_password_page .list_ele .account_content{
    background-color: rgba(206, 235, 255,0.15);

}
.forget_password_page .list_ele .account{
    background-color: transparent !important;
}
.forget_password_page .auth_code_content{
    background-color: rgba(206, 235, 255,0.15);
    margin-top: 0.5rem;
    border-radius: 0.3rem;
    padding: 0 0.5rem;
}
.forget_password_page .auth_code_content input{
    background-color: transparent;
    margin-top: 0;
}
.forget_password_page .get_auth_code_btn{
   color: #fb59af;
}
.forget_password_page .submit_btn{
    padding: 0.75rem;
    width: 100%;
    text-align: center;
    color: #ffffff;
    margin-top: 2rem;
    border-radius: 0.3rem;
    font-size: 1rem;
}
.forget_password_page select{
    background: transparent;
    border: none;
}


