.add_wx_page .add_wx_content{
    padding: 1rem;
    background-image: url('../../image/mine/mine_bg.png');
    background-size: 100% 100%;
    color: #ffffff;
    background-attachment: fixed;
}
.add_wx_page .list_ele{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    margin-top: 1.5rem;
}
.add_wx_page .list_ele label{
    font-size: 1.1rem;
}
.add_wx_page .list_ele input[type='text']{
    margin-top: 0.5rem;
    background-color: rgba(206, 235, 255,0.15);
    padding: 0.5rem;
    font-size: 1rem;
    border: none;
    border-radius: 0.3rem;
    color: #ffffff;
}
.add_wx_page .submit_btn{
    padding: 0.75rem;
    width: 100%;
    text-align: center;
    color: #ffffff;
    margin-top: 2rem;
    border-radius: 0.3rem;
    font-size: 1rem;
}
.add_wx_page .upload_img_content{
    position: relative;
    margin-top: 1rem;
}
.add_wx_page .wx_img{
    height: 6rem;
    width: 6rem;
    position:absolute;
    top: 0;
    left: 0;
    opacity: 0;
}
.add_wx_page .camera{
    height: 6rem;
    /* background-color: red; */
}


