.club_page .club_content{
    padding: 1rem;
    background-image: url('../../image/mine/mine_bg.png');
    background-size: 100% 100%;
    color: #ffffff;
    background-attachment: fixed;
}
.club_logo_content{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    padding: 1rem;
}
.club_logo_icon{
    /* width: 5rem; */
}
.club_rank{
    text-align: center;
    width: 100%;
    margin-top: 0rem;
}
.list_content_title{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    margin: 2rem 0 0;
    padding-bottom: 0.5rem;
    border-bottom: 1px solid #ffffff;
}
.list_content_title span{
    display: inline-block;
    width: 50%;
    text-align: center;
}
.list_content li{
    padding: 0.5rem 0;
    border-bottom: 1px solid #666666;
}
.list_content li span{
    display: inline-block;
    width: 50%;
    text-align: center;
    color: #cccccc;
}


