.is_link{
  position: relative;
}
.is_link::after{
  content: '';
  width: 0.4rem;
  height: 0.4rem;
  position: absolute;
  top: 50%;
  right: 0.75rem;
  margin-top: -0.2rem;
  background: transparent;
  border: 1px solid #dddddd;
  border-top: none;
  border-right: none;
  z-index: 2;
  border-radius: 0;
  -webkit-transform: rotate(-135deg);
  -ms-transform: rotate(-135deg);
      transform: rotate(-135deg);
}

.under-line-all{
  position: relative;
}
.under-line-all::before{
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #f2f2f4;
}

.top_shadow{
  width: 100%;
  position: relative;
  z-index: 99;
}
.top_shadow::before{
  content: '';
  height: 1rem;
  width: 100%;
  position: absolute;
  top: 0rem;
  z-index: -1;
  left: 0;
  -webkit-box-shadow: 0 -0.4rem 0.4rem #0c2760;
          box-shadow: 0 -0.4rem 0.4rem #0c2760;
}
.bottom_shadow{
  width: 100%;
  position: relative;
  z-index: 99;
}
.bottom_shadow::before{
  content: '';
  height: 1rem;
  width: calc(100% - 0.5rem);
  position: absolute;
  bottom: 1rem;
  z-index: -1;
  left: 0.25rem;
  -webkit-box-shadow: 0 0.8rem 0.8rem #1f8cf4;
          box-shadow: 0 0.8rem 0.8rem #1f8cf4;
}
