.welfare_islands_component{
    padding-bottom: 2rem;
}
.welfare_islands_component .group_content_top_count{
    margin-top: 3rem;
    padding: 0 0.5rem;
    position: relative;
    background: #151d30;
}
.welfare_islands_component .group_content_1, .welfare_islands_component .group_content_2{
    border: 1px solid #00c0ff;
    border-top: none;
    -webkit-box-shadow: 0 0 2rem #00c0ff inset;
            box-shadow: 0 0 2rem #00c0ff inset;
    margin-top: 3rem;
    padding: 0 0.5rem;
    padding-bottom: 1.5rem;
    position: relative;
}
/* 四角定位 */
.welfare_islands_component .four_corner_sm div{
    position: absolute;
    height: 1rem;
    width: 1rem;
    border-left: 1.5px solid #00d2ff;
    border-bottom: 1.5px solid #00d2ff;
}
.welfare_islands_component .four_corner div{
    position: absolute;
    height: 2rem;
    width: 2rem;
    border-left: 3px solid #00d2ff;
    border-bottom: 3px solid #00d2ff;
}
.welfare_islands_component .left_top{
    top: -1.5px;
    left: -2px;
    -webkit-transform: rotate(90deg);
        -ms-transform: rotate(90deg);
            transform: rotate(90deg);
}
.welfare_islands_component .left_bottom{
    bottom: -1.5px;
    left: -2px;
}
.welfare_islands_component .right_top{
    top: -1.5px;
    right: -2px;
    -webkit-transform: rotate(180deg);
        -ms-transform: rotate(180deg);
            transform: rotate(180deg);
}
.welfare_islands_component .right_bottom{
    bottom: -1.5px;
    right: -2px;
    -webkit-transform: rotate(270deg);
        -ms-transform: rotate(270deg);
            transform: rotate(270deg);
}
.welfare_islands_component .group_content_1_btn_group,.welfare_islands_component .group_content_2_btn_group{
    position: absolute;
    top: -1.5rem;
    left: 0;
    width: 100%;
    height: 3rem;
    padding: 0 2rem;
    z-index: 9;
}
.welfare_islands_component .group_content_1_btn_group div,.welfare_islands_component .group_content_2_btn_group div{
    font-size: 0.8rem;
    background-color: transparent;
    padding: 0 0.75rem;
    color: #00c0ff;
}
/* 阴影 */
.welfare_islands_component .group_content_1_btn_group_active,.welfare_islands_component .group_content_2_btn_group_active{
    position: relative;
    font-size: 0.9rem !important;
    color: #ffffff !important;
}
.welfare_islands_component .group_content_1_btn_group_active::before, .welfare_islands_component .group_content_2_btn_group_active::before{
    content: '[';
    height: 100%;
    width: 1rem;
    position: absolute;
    font-size: 1.3rem;
    left: 0;
    color: #00c0ff;
    top: -38%;
    /* background-color: red; */
}
.welfare_islands_component .group_content_1_btn_group_active::after, .welfare_islands_component .group_content_2_btn_group_active::after{
    content: ']';
    height: 100%;
    width: 1rem;
    position: absolute;
    font-size: 1.3rem;
    right: 0;
    top: -38%;
    color: #00c0ff;
    text-align: right;
}
.welfare_islands_component .text-color-blue{
    color: #00c0ff;
}

.welfare_islands_component .group_content_2{
    margin-bottom: 2rem;
}
.welfare_islands_component .group_content_2_btn_group div{
    font-size: 0.6rem;
    padding: 0.3rem;
}
.welfare_islands_component .group_content_2_btn_group_active{
    padding: 0.5rem !important;
}
.welfare_islands_component .group_content_2_btn_group_active::before{
    top: 0%;
}
.welfare_islands_component .group_content_2_btn_group_active::after{
    top: 0%;
}
.welfare_islands_component .inset_content{
    padding:1.5rem 0.5rem;
    color:#00c0ff;
    width: calc(100% / 2 );
}
.welfare_islands_component .inset_content:nth-of-type(1){
    border-right: 1px solid #004862
}
.welfare_islands_component .inset_content div , .welfare_islands_component .inset_content p{
    color:#00c0ff;
    font-size: 0.9rem;
}
.welfare_islands_component .inset_content p{
    font-size: 0.8rem;
}
.welfare_islands_component .pool_text_color{
    color: #fff;
    font-size: 1.3rem;
    text-shadow: 0 0 18px #ff0473,0 0px 18px #ffffff;
}
.welfare_islands_component .pool_eth_icon{
    height: 1.5rem;
}


