.transition_record_page .transition_record_content{
    background-image: url('../../image/transition/transition_bg.png');
    background-size: 100% 100%;
}
.transition_record_page .font-size-14{
    font-size:0.8rem !important;
}
.transition_record_page .font-size-16{
    font-size: 1rem !important;
}
.transition_record_page .text-color-pink{
    color: #fe028a;
}
.transition_record_page .deep_blue{
    color: #00405f !important;
}
.transition_record_page .transition_record_page_header{
    height: 3rem;
    position: relative;
    padding: 0 0.75rem;
    background-color: #070e1b;
}
.transition_record_page .header_tab{
    width: 6rem;
    text-align: center;
    -ms-flex-pack: center;
        justify-content: center;
    position: absolute;
    left: calc(50% - 3rem);
    height: 60%;
    color: #c8c8c5;
    font-size: 1rem;
}
.transition_record_page .list_ele{
    padding: 0.75rem 1.5rem;
    color: #d6d6d6;
    background-color: rgba(206, 235, 255, 0.15);
    margin-top: 1rem;
}
.transition_record_page .list_ele:nth-of-type(1){
    margin-top: 0;
}
.transition_record_page .record_icon{
    height: 1.5rem;
}
.transition_record_page .list_ele_title{
    padding: 0.5rem 0;
    border-bottom: 1px dashed #5b79ae;
}
.transition_record_page .create_time{
    padding: 0.5rem 0;
    text-align: left;
}

/* 弹出框 */
.transition_record_page .alert_content_record{
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: 99;
    background-color: rgba(0, 0, 0, 0.5);
    display: none;
}
.transition_record_page .alert_content{
    width: 80%;
    /* height: 50%; */
    background-color: #fff;
    position: absolute;
    top: 3rem;
    right: 0.75rem;
    border-radius: 0.5rem;
    padding: 0.75rem;
    font-size: 0.8rem;
}
.transition_record_page .alert_content>div{
    margin-top: 1rem;
}
.transition_record_page .alert_content>div:nth-of-type(1){
    margin-top: 0;
}
.choose_ele_content{
    text-align: left;
}
.transition_record_page .btn_group span{
    color: #333333;
    display: inline-block;
    margin-top: 0.5rem;
    padding: 0.5rem 0rem;
    width: calc(100% / 3 - 5px);
    text-align: center;
    border-radius: 0.3rem;
    margin-left: .25rem;
    background-color: #ebebeb;
}
.transition_record_page .fun_btn_group span{
    width: 50%;
    padding: 0.7rem 0;
    font-size: 0.9rem;
}
.transition_record_page .fun_btn_group span:nth-of-type(1){
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
}
.transition_record_page .fun_btn_group span:nth-of-type(2){
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
}
.transition_record_page .fun_btn_group_active{
    color: #fff !important;
    background-color: #ff1daa !important;
}
.transition_record_page .choose_active{
    background-color: #fed6ec !important;
    color: #ff0192 !important;
    position: relative;
}
.transition_record_page .choose_active img{
    position: absolute;
    right: 0;
    bottom: 0;
    height: 50%;
}


