.index_page *{
    color: #ffffff;
}
.alert_p{
    color: #051124 !important;
}
.index_page{
    background-image: url('../../image/index_bg.png');
    background-size: 100% 100%;
    background-attachment: fixed;
}
.index_page_header{
    height: 3rem;
}
.index_page_content_header,.index_page_content_header_msg_bar{
   background-color:  #051124;
}
.index_page .logo_icon{
    height: 2rem;
}
.index_page .recharge_btn{
    height: 2rem;
}
.index_page .index_page_header_content{
    padding: 0.75rem;
}
.index_page .tabbar_header{
    margin-top: 0.75rem;
    padding: 0 1.5rem;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    position: relative;
}
.index_page .tabbar_header div{
    font-size: 0.9rem;
    position: relative;
    text-align: center;
    padding: 0.3rem 0.5rem;
}
.index_page .tabbar_header::after{
    content: '';
    height: 2px;
    width: 100%;
    background-color: #02070e;
    border-bottom: 1px solid #0e192b;
    position: absolute;
    left: 0;
    bottom: 0;
}
.index_page .tabbar_header div::before{
    content: '';
    height: 2px;
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 0;
}
.index_page .tabbar_header_active{
    position: relative;
    color: #00dffe !important;
}
.index_page .tabbar_header_active::before{
    background-image: url('../../image/header_active.png');
    background-size: 100%;
    z-index: 9;
    bottom: -7px !important;
    height: 10px !important;
}
.index_page .timer_bar{
    padding: 1rem 1rem;
    font-size: 0.8rem;
}
.index_page .timer_bar_icon{
    height: 1.3rem;
    /* margin-right: 0.5rem; */
}
.index_page .question_icon{
    height: 1rem;
}

.index_page .index_page_main{
    padding: 0 1.5rem ;
    margin-top: 1rem;
}
.index_page .banner1{
    width: 100%;
    margin: 1rem 0 0;
}
.index_page .eth_icon{
    height: 3rem;
}
.index_page .banner_ETH_num{
    font-size: 1.5rem;
}
.index_page .banner_time{
    font-size: 1.2rem;
    padding: 0.5rem;
    text-align: center;
}
.index_page .notice_bar{
    width: 100%;
    border: 1px solid #1d2637;
    border-radius: 0.5rem;
    padding:0.5rem  0.75rem; 
    color: #bababa;
    font-size: 0.9rem;
    height: 2rem;
    overflow: hidden;
}
.index_page .notice_icon{
    height: 1.5rem;
}
.index_page .notice_bar span{
    color: #bababa;
    margin-left: 0.75rem;
}
.index_page .group_content_1,.index_page .group_content_2{
    border: 1px solid #00c0ff;
    border-top: none;
    -webkit-box-shadow: 0 0 2rem #00c0ff inset;
            box-shadow: 0 0 2rem #00c0ff inset;
    margin-top: 3rem;
    padding: 0 0.5rem;
    padding-bottom: 1.5rem;
    position: relative;
}
/* 四角定位 */
.index_page .four_corner div{
    position: absolute;
    height: 2rem;
    width: 2rem;
    border-left: 3px solid #00d2ff;
    border-bottom: 3px solid #00d2ff;
}
.index_page .left_top{
    top: -1.5px;
    left: -2px;
    -webkit-transform: rotate(90deg);
        -ms-transform: rotate(90deg);
            transform: rotate(90deg);
}
.index_page .left_bottom{
    bottom: -1.5px;
    left: -2px;
}
.index_page .right_top{
    top: -1.5px;
    right: -2px;
    -webkit-transform: rotate(180deg);
        -ms-transform: rotate(180deg);
            transform: rotate(180deg);
}
.index_page .right_bottom{
    bottom: -1.5px;
    right: -2px;
    -webkit-transform: rotate(270deg);
        -ms-transform: rotate(270deg);
            transform: rotate(270deg);
}
.index_page .group_content_1_btn_group,.index_page .group_content_2_btn_group{
    position: absolute;
    top: -1.5rem;
    left: 0;
    width: 100%;
    height: 3rem;
    padding: 0 2rem;
    z-index: 9;
}
.index_page .group_content_1_btn_group div,.index_page .group_content_2_btn_group div{
    font-size: 0.8rem;
    background-color: transparent;
    padding: 0 0.75rem;
    color: #00c0ff;
}
/* 阴影 */
.index_page .group_content_1_btn_group_active,.index_page .group_content_2_btn_group_active{
    position: relative;
    font-size: 0.9rem !important;
    color: #ffffff !important;
}
.index_page .group_content_1_btn_group_active::before,.index_page .group_content_2_btn_group_active::before{
    content: '[';
    height: 100%;
    width: 1rem;
    position: absolute;
    font-size: 1.3rem;
    left: 0;
    color: #00c0ff;
    top: -38%;
    /* background-color: red; */
}
.index_page .group_content_1_btn_group_active::after,.index_page .group_content_2_btn_group_active::after{
    content: ']';
    height: 100%;
    width: 1rem;
    position: absolute;
    font-size: 1.3rem;
    right: 0;
    top: -38%;
    color: #00c0ff;
    text-align: right;
}
.index_page .text-color-blue{
    color: #00c0ff;
}

.index_page .group_content_2{
    margin-bottom: 2rem;
}
.index_page .group_content_2_btn_group div{
    font-size: 0.6rem;
    padding: 0.3rem;
}
.index_page .group_content_2_btn_group_active{
    padding: 0.5rem !important;
}
.index_page .group_content_2_btn_group_active::before{
    top: 0%;
}
.index_page .group_content_2_btn_group_active::after{
    top: 0%;
}

.index_page .footer_icon{
    height: 1.5rem;
}
.index_page .footer_active p{
    color: #d44b9c !important;
}
.index_page footer div{
    padding: 0.5rem 0;
    width: calc(100% / 4);
} 

/* 轮播样式 */
.index_page  .swiper-container {
    width: 100%;
    line-height: 2rem !important;
}
.index_page  .swiper-slide {
    width: 100%;
    height: 2rem !important;
    line-height: 2rem !important;
}
/* 头部轮播 */
.index_page  .notice_content .swiper-slide{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: start;
        justify-content: flex-start;
    color: #ffffff;
}
.notice_bar .notice_word{
    width: 90%;
    white-space: nowrap;
    display: block;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    overflow: hidden;
}
.index_page  .balance_content{
    display: block;
    width: 3rem;
    overflow: hidden;
    white-space: nowrap;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}
.index_page  .airload_descript{
    display: none;
}
.index_page_header{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 99;
}
.index_page_content{
    padding-top: 9rem;
}
.airload_content p{
    color: #000000;
}


