.payment_page .payment_page_content{
    background-image: url('../../image/wallet/bg-wallet.png');
    background-size: 100% 100%;
    padding: 0.75rem;
}
.payment_page .text-size-14{
    font-size: 0.8rem;
}
.payment_page .text-size-16{
    font-size:1rem;
}
.payment_page .text-color-white{
    color: #ffffff;
}
.payment_page {
    /* padding: 0.75rem; */
}
.payment_page .text-color-red{
    color: #df0127;
}
.payment_page .margin-right-4{
    margin-right: 0.2rem;
}
.payment_page .payment_page_content *{
    color: #ffffff;
    margin-top: 0.75rem;
}

.payment_page .receovables_page{
    padding-top: 4rem;
    display: -ms-flexbox;
    display: flex;
    /* justify-content: center; */
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
}
.payment_page .erweima{
    width: 8rem;
}
.payment_page .title_name{
    font-size: 1rem;
