    margin: 1rem auto;
    text-align: center;
    border-radius: 0.3rem;
    width: 70%;
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    white-space: nowrap;
}
.payment_page .submit_btn{
    margin: 0rem auto;
    text-align: center;
    border-radius: 0.3rem;
    width: 70%;
    padding: 0.5rem;
    color: #ffffff;
    font-size: 1rem;
}
.payment_page .input_content{
    background-color: rgba(206, 235, 255, 0.15); 
    border-radius: 0.3rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}
.payment_page .all_btn{
    min-width: 3rem;
    text-align: center;
    margin-top: 0;
    color: #00d9ff;
    font-size: 0.9rem;
}
.payment_page input{
    height: 100%;
    width: 100%;
    background-color: transparent;
    border: none;
    padding: 0.75rem;
    font-size: 0.9rem;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin-top: 0 !important;
}
.payment_page .waring_bar{
    font-size: 0.8rem;
    color: #b7b7b7;
}
.payment_page .submit_btn{
    padding: 0.5rem;
    border-radius: 0.3rem;
    width: 100%;
    margin-top: 2rem;
}

.payment_page .money_bar{
    color: #b7b7b7;
    text-align: right;
}


