.index_wallet_page *{
    color: #ffffff;
    text-align: left;
}
.index_wallet_main{
    padding-bottom: 1rem;
}
.header_left{
    color: #959898;
}
.index_wallet_page .index_wallet_content{
    background: url('../../image/wallet/bg-wallet.png') no-repeat;
    background-size: 100% 100%;
}
.index_wallet_page .icon-eth{
    height: 1.6rem;
    display: inline-block;
    vertical-align: middle
}
.index_wallet_page .top-num{
    text-align: center;
    font-size: 1.5rem;
    padding: 1rem 0;
    color:#fff;
}
.index_wallet_page .top-num div{
    text-align: center
}
.index_wallet_page .top-num p{
   color:#2c9cbc;
   font-size: 0.9rem;
   text-align: center;
}
.index_wallet_page .nav-icon{
   background: rgba(255,255,255,0.2);
   border-radius: 10px;
   padding:0.5rem 0;
    width: 90%;
    margin:0 auto;
}
.index_wallet_page .nav-icon>div{
   width: calc(100% / 3);
   text-align: center !important;
   font-size: 0.9rem;
}
.index_wallet_page .nav-icon>div>div{
   text-align: center !important;
}
.index_wallet_page .nav-icon>div img{
   width: 4rem;
   display: inline-block;
}
.index_wallet_page .trans-wrap{
   background: rgba(255,255,255,0.1);
   border-radius: 10px;
   width: 90%;
   margin:0 auto;
   margin-top: 1.5rem;
   padding:0.5rem;
}
.index_wallet_page .trans-wrap .title , .index_wallet_page .trans-wrap .sub-title{
   padding:0 0 0.5rem;
   font-size: 1rem;
   color:#fff;
   font-weight: 500;
   border-bottom: 1px dotted #5779af;

}
.index_wallet_page .txt-red{
   color: #fe0288;
}
.index_wallet_page .txt-blue{
   color:#019dc8;
}
.index_wallet_page .table-td{
   padding:0.5rem 0 0;
   font-size: 0.8rem;
}
.index_wallet_page .table-row ,.index_wallet_page .table-body{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.index_wallet_page .table-row .table-td{
   width: calc(100% / 3);
}
.index_wallet_page .table-body .table-td{
   width: calc(100% / 3);
}
.index_wallet_page .table-body .table-td:nth-of-type(1){
   font-size: 1.1rem;
   word-break: break-all
}
.index_wallet_page .table-body .table-td:nth-of-type(2){
   -ms-flex: 1 1;
       flex: 1 1;
   -o-text-overflow: ellipsis;
      text-overflow: ellipsis;
   overflow: hidden;
}
.index_wallet_page .header_rgiht_content{
    font-size: 1rem;
    color: #959898;
}
.index_wallet_page .near_future .sub-title{
    padding-top: 0.5rem;
}
.index_wallet_page .user_balance {
    padding: 0.5rem 0;
    width: 90%;
    margin: 0 auto;
}

.index_wallet_page .user_balance>div {
    width: calc(100% / 2);
    text-align: center !important;
    font-size: 0.9rem;
}

.index_wallet_page .user_balance>div img{
    width:3rem;
}


