.settile_account_content{
    min-height: 100%;
    padding: 1rem;
    background-image: url('../../image/mine/mine_bg.png');
    background-size: 100% 100%;
    color: #ffffff;
    background-attachment: fixed;
}
.settile_account_page .list_ele{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    margin-top: 1.5rem;
}
.settile_account_page .list_ele label{
    font-size: 1.1rem;
}
.settile_account_page .list_ele input{
    margin-top: 0.5rem;
    background-color: rgba(206, 235, 255,0.15);
    padding: 0.5rem;
    font-size: 1rem;
    border: none;
    border-radius: 0.3rem;
    color: #ffffff;
}
.settile_account_page .submit_btn{
    padding: 0.75rem;
    width: 100%;
    text-align: center;
    color: #ffffff;
    margin-top: 2rem;
    border-radius: 0.3rem;
    font-size: 1rem;
}


