.payment_type_page .payment_type_content{
    background-image: url('../../image/mine/mine_bg.png');
    background-size: 100% 100%;
    color: #ffffff;
    background-attachment: fixed;
}
.payment_type_page .header_right{
    font-size: 1rem;
}
.payment_type_page .block-list{
    border-bottom: 1px solid #ffff;
    padding: 0.1rem 0;
}
.payment_type_page .fl{
	float: left;
}
.payment_type_page .fr{
	float: right;
}
.payment_type_page .list-body{
    overflow : hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    margin-bottom: 0.1rem;
}
.payment_type_page .list-head{
    font-size: 0.85rem;
    color: #00f6ff;
    font-weight: 500;
}
.payment_type_page .business-tab{
    line-height: 2rem;
    height: 2rem;
    background: #17628342;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    color: #00fff0;
    font-size: 0.8rem;
}
.payment_type_page .tab-col-xs-4{
    display: inline-block;
    -ms-flex: 1 1;
        flex: 1 1;
    text-align: center;
    float: left;
}
.payment_type_page .block-info{
    color: #00fff0;
    display: -ms-flexbox;
    display: flex;
    font-size: 0.7rem;
}
.payment_type_page .info-text {
    padding: 0.2rem 0.2rem 0 0;
}
.payment_type_page .info-img img {
    width: 6rem;
}
.payment_type_page .card-group{
    margin-top: 1rem;
}
.payment_type_page .card-block{
    background-size: 100% 100%;
    background-repeat: no-repeat;
    margin: 0.5rem;
    padding: 0.75rem;
    color: #fff;
    font-size: .9rem;
}
.payment_type_page .cark-title {
    overflow: hidden;
}
.payment_type_page .cark-title img {
    width: 1rem;
    margin-right: 0.5rem;
}
.payment_type_page .card-name{
    margin: 0.2rem 0 0.3rem 1.3rem;
}
.payment_type_page .card-number{
    margin-left: 1.3rem;
    font-size: 1.2rem;
}
.payment_type_page .card-userandoprate{
    margin-top: 0.5rem;
    margin-left: 1.3rem;
}
.payment_type_page .card-userandoprate button {
  border: 1.5px solid #FFFFFF;
    border-radius: 0.2rem;
    width: 3rem;
    text-align: center;
    background: transparent;
    color: #ffff;
}
.payment_type_page .paytype-img{
  width: 6rem;
    margin: 0 auto;
    display: block;
}
.payment_type_page .text-block{
    width: 7rem;
    height: 1.8rem;
    padding-top: 0.4rem;
    margin: 0.3rem auto;
    text-align: center;
    background-color: #230f43;
}
.payment_type_page .bigbutton {
    width: 90%;
    height: 2rem;
    font-size: 0.8rem;
    color: #fff;
    margin: 0 5%;
    border-radius: 0.2rem;
    border: none;
    margin-top: 1rem;
    background: -webkit-gradient(linear,left top, right top,from(#1f9bfb),to(#e74fc5));
    background: -webkit-linear-gradient(left,#1f9bfb,#e74fc5);
    background: -o-linear-gradient(left,#1f9bfb,#e74fc5);
    background: linear-gradient(to right,#1f9bfb,#e74fc5);
}
.payment_type_page .addtypeul{
    margin: 1rem;
}
.payment_type_page .addtypelist{
    display: -ms-flexbox;
    display: flex;
    height: 2rem;
    -ms-flex-align: center;
        align-items: center;
    margin-bottom: 0.1rem;
    background-color: #17628342;
    padding: 0.5rem;
    border-radius: 0.2rem;
    position: relative;
}
.payment_type_page .addtypelist span{
    margin-left: 0.5rem;
}
.payment_type_page .addtype-leftimg{
    width: 0.7rem;
}
.payment_type_page .addtype-rightimg{
    width:1.2rem;
    position: absolute;
    right: 0.3rem;
}
.payment_type_page .yl_gs_img{
    background-image: url('../../image/mine/group4.png');
}
.payment_type_page .zfb_gs_bank{
    background-image: url('../../image/mine/group5.png');
}
.payment_type_page .wx_gs_bank{
    background-image: url('../../image/mine/group6.png');
}
/* 没有数据 */
.payment_type_page .addpaytype{
    margin-top: 7rem;
    height: 5rem;
}
.payment_type_page .waring_bar{
    font-size: 1rem;
    color: #fff;
    margin: 1rem 0;
    padding: 0.53rem 1.5rem;
    background-image: -webkit-linear-gradient(0deg, transparent,rgba(6, 172, 249, 0.2), rgb(85, 67, 250, 0.3), rgb(248, 89, 176, 0.2),transparent);
}
.payment_type_page .add_btn{
    width: calc(100% - 3rem);
    padding: 0.5rem;
    text-align: center;
    font-size: 1rem;
    border-radius: 0.3rem;
}


