.login_page .login_page_content{
    background-image: url('../../image/mine/mine_bg.png');
    background-size: 100% 100%;
    color: #ffffff;
    background-color: blue;
    /* background-attachment: fixed; */
    padding: 1rem;
}
.login_page .logo{
    width: 70%;
    margin: 3rem auto;
}
.login_page .account{
    width: 1.5rem;
    min-width: 1.5rem;
}
.login_page .list_ele{
    padding: 0.5rem;
    background-color: rgba(218, 246, 255, 0.15);
    border-radius: 0.3rem;
    width: 100%;
    margin-top: 1rem;
    border: 1px solid transparent;
    position:relative;
}
.login_page .list_ele input{
    width: 100%;
    margin-left: 1rem;
    background-color: transparent;
    border: none;
    color: #ffffff;
    font-size: 1rem;
    padding: 0.3rem;
}
.login_page .forget_password{
    text-decoration: underline;
}
.login_page .forget_password_bar{
    font-size: 1rem;
    margin-top: 1rem;
}
.login_page input{
    min-width: 1.2rem;
}
.login_page .checked_box:checked{
    background-color: #03a9f4;
    border: solid 1px #03a9f4;
    text-align: center;
    background-clip: padding-box;
}
.login_page .checked_box {
    width: 1.2rem;
    height: 1.2rem;
    background-color: #ffffff;
    border: solid 1px #dddddd;
    /* -webkit-border-radius: 0.6rem; */
    /* border-radius: 0.75rem; */
    font-size: 0.8rem;
    margin: 0;
    padding: 0;
    position: relative;
    display: inline-block;
    vertical-align: top;
    cursor: default;
    -webkit-appearance: none;
    -webkit-user-select: none;
    -moz-user-select: none;
     -ms-user-select: none;
         user-select: none;
    -webkit-transition: background-color ease 0.1s;
    -o-transition: background-color ease 0.1s;
    transition: background-color ease 0.1s;
}
.login_page .checked_box:checked:after, .checked_box.checked:after {
    content: '';
    width: 0.5rem;
    height: 0.3rem;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -0.25rem;
    margin-top: -0.25rem;
    background: transparent;
    border: 1px solid #ffffff;
    border-top: none;
    border-right: none;
    z-index: 2;
    border-radius: 0;
    -webkit-transform: rotate(-45deg);
    -ms-transform: rotate(-45deg);
        transform: rotate(-45deg);
}
.login_page .remember_password{
    margin-left: 0.5rem;
}
.login_page .login_btn{
    padding: 0.75rem;
    width: 100%;
    text-align: center;
    color: #ffffff;
    margin-top: 2rem;
    border-radius: 0.3rem;
    font-size: 1rem;
}
.login_page .phone_login{
    text-decoration: underline;
    text-align: center;
    color: #ffffff;
    font-size: 1rem;
    position: fixed;
    bottom: 1rem;
    width: 100%;
}
.login_page .register_btn{
    text-align: right;
    color: #ffffff;
    font-size: 1rem;
}
.login_page .ant-spin{
    position: fixed;
    z-index: 9999;
    top: 45%;
    left: 45%;
}


