.mine_page{
    background-image: url('../../image/mine/mine_bg.png');
    background-size: 100% 100%;
    color: #ffffff;
    background-attachment: fixed;
}
.mine_page .text-color-gray{
    color: #b7b7b8;
}
.mine_page .user_msg_content{
    padding: 1rem;
    padding-top: 3rem;
    padding-bottom: 3rem;
    background-color: rgba(0, 56, 120, 0.2);
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.mine_page .user_msg_center{
    width: 100%;
}
.mine_page .user_icon{
    min-width: 5rem;
    width: 5rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    position: relative;
}
.mine_page .default_icon{
    min-width: 4rem;
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
}
.mine_page .user_msg{
    width: calc(100% - 5rem);
}
.mine_page .erweima_content{
    min-width: 2rem;
}
.mine_page .erweima{
    width: 1.5rem;
}
.mine_page .invited_label{
    color: #00d2ff;
    font-size: 0.9rem;
    margin-top: 0.2rem;
}
.mine_page .user_name{
    font-size: 1rem;
}
.user_name_address_content{
    width: 70%;
}
.user_name_address_content>*{
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    white-space: nowrap;
}
.mine_page .user_num_bar{
    width: 100%;
    padding: 1.5rem 1rem;
    /* margin-top: 1rem; */
}
.user_num_ele{
    width: 25%;
}
.mine_page .user_num_ele_num{
    font-size: 0.8rem;
    text-align: center;
    overflow: hidden;
    /* max-width: 4rem; */
    /* text-overflow: ellipsis; */
    white-space: nowrap;
}
.mine_page .user_num_ele_label{
    font-size: 0.9rem;
    color: #a0a2a6;
    text-align: center;
}
.mine_page .tool_bar_content{
    padding: 1rem;
    margin-top: -3.5rem;
}
.mine_page .tool_bar_title{
    padding: 0.5rem;
    font-size: 1rem;
    color: #ffffff;
    background-color: rgba(218, 246, 255, 0.2);
    border-top-left-radius: 0.3rem;
    border-top-right-radius: 0.3rem;
    border-bottom: 1px dashed #094d77;
}
.mine_page .tool_bar{
    /* padding: 0.75rem; */
    padding-top: 0;
    background-color: rgba(218, 246, 255, 0.15);
    padding-bottom: 1rem;
    border-bottom-left-radius: 0.3rem;
    border-bottom-right-radius: 0.3rem;
}
.mine_page .tool_bar li{
    width: 25%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    margin-top: 0.75rem;
}
.mine_page .tool_bar li p{
    font-size: 0.9rem;
    margin-top: 0.2rem;
}
.mine_page .list_icon{
    width: 60%;
}
.mine_page .quotient_content{
    margin-top: -0.5rem;
}
.mine_page .login_out{
    padding: 0.75rem;
    color: #ffffff;
    border-radius: 0.3rem;
    text-align: center;
    font-size: 1rem;
    margin:1rem;
}
.mine_page .upload_user_icon_btn{
    width: 80%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 10%;
    border-radius: 50%;
    opacity: 0;
}



