.transition_page_content{
    background-image: url('../../image/transition/transition_bg.png');
    background-size: 100% 100%;
}
.transition_page .font-size-14{
    font-size:0.7rem !important;
}
.transition_page .font-size-16{
    font-size: 1rem !important;
}
.transition_page .deep_blue{
    color: #00405f !important;
}
.transition_page .header_tab{
    position: absolute;
    left: calc(50% - 3rem);
    /* height: 100%; */
    display: -ms-flexbox;
    display: flex;
    color: #c8c8c5;
    font-size: 1rem;
    /* margin-top: 5%; */
}
.transition_page .buy_icon,.transition_page .sale_icon{
    height:60%;
    padding: 0 0.75rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    background-color: #50575d;
}
.transition_page .buy_icon{
    border-top-left-radius: 0.3rem;
    border-bottom-left-radius: 0.3rem;
}
.transition_page .sale_icon{
    border-top-right-radius: 0.3rem;
    border-bottom-right-radius: 0.3rem;
}
.transition_page .title_btn_active{
    color: #ffffff !important;
    background-color: #ff1daa !important;
}
.transition_page .record_icon{
    height: 1.5rem;
}
.transition_page .transition_page_content{
    padding: 0 0.75rem;
}
.transition_page .transition_page_content_header{
    font-size: 1rem;
    padding: 0.75rem 0;
    width: 100%;
    position: relative;
    display: -ms-flexbox;
    display: flex;
}
.transition_page .transition_page_content_header::after{
    content: '';
    height: 3px;
    width: 100%;
    background-color: #000000;
    position: absolute;
    left: 0;
    bottom: 0;
    border-radius: 3px !important;
}
.transition_page .transition_page_content_header div{
    position: relative;
    height: 100%;
    padding: 0 1rem;
}
.transition_page .transition_page_content_header div::before{
    content: '';
    height: 2px;
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 0;
}
.transition_page .transition_page_content_header_active{
    /* position: relative; */
    color: #00dffe !important;
}
.transition_page .transition_page_content_header_active::before{
    background-image: url('../../image/header_active.png');
    background-size: 100% 100%;
    z-index: 9;
    bottom: calc(-0.75rem - 8px) !important;
    height: 10px !important;
}
.transition_page .price_bar{
    padding: 0.75rem 0;
    color: #ffffff;
    text-align: left;
}
.transition_page .list_ele{
    width: 100%;
    padding: 0.75rem;
    border: 1px solid #00f7fe;
    background-color: #aabed2;
    margin-top: 0.75rem;
    position: relative;
}
.transition_page .list_ele_icon{
    min-width: 2.8rem;
    width: 2.8rem;
    height: 2.8rem;
    border-radius: 50%;
}
.transition_page .other_icon{
    height: 0.7rem;
    margin-top: 0.3rem;
}
/* .auth_status{
    background-color: #b9137a;
    color: #ffffff;
    border-radius: 0.3rem;
    font-size: 0.6rem;
    padding: 0.2rem;
} */
.transition_page .list_ele_detail{
    width: calc(100% - 2.8rem);
    padding: 0 0.75rem;
}
.transition_page .user_name{
    font-size: 1rem;
    color: #000000;
    margin-right: 0.5rem;
}
.transition_page .pay_icon{
    height: 1.3rem;
    /* margin-left: 0.3rem; */
}
.transition_page .price_content{
    font-size: 1rem;
    color: rgba(0, 0, 0, 0.65);
}
.transition_page .user_msg_bar{
    white-space: nowrap;
}
.transition_page .user_msg_bar span{
    padding: 0 0.5rem;
    font-size: 0.8rem;
    position: relative;
}
.transition_page .user_msg_bar span:nth-of-type(1){
    padding-left: 0;
}
.transition_page .user_msg_bar span::after{
    content: '';
    height: 100%;
    width: 1px;
    background-color: #ffffff;
    position: absolute;
    right: 0;
    top: 0;
}
.transition_page .user_msg_bar span:nth-last-of-type(1):after{
    display: none;
}
.transition_page .buy_btn{
    padding: 0.3rem 1rem;
    color: #ffffff;
    background-color: #000238;
    border-radius: 0.3rem;
}
.transition_page .buy_btn_content{
    margin-top: 0.3rem;
}
.transition_page .around div{
    position: absolute;
    height: 1.5rem;
    width: 1.5rem;
    border-left: 3px solid #00f7fe;
    border-bottom: 3px solid #00f7fe;
}
.transition_page .left_top{
    top: -2px;
    left: -2px;
    -webkit-transform: rotate(90deg);
        -ms-transform: rotate(90deg);
            transform: rotate(90deg);
}
.transition_page .left_bottom{
    bottom: -2px;
    left: -2px;
}
.transition_page .right_top{
    top: -2px;
    right: -2px;
    -webkit-transform: rotate(180deg);
        -ms-transform: rotate(180deg);
            transform: rotate(180deg);
}
.transition_page .right_bottom{
    bottom: -2px;
    right: -2px;
    -webkit-transform: rotate(270deg);
        -ms-transform: rotate(270deg);
            transform: rotate(270deg);
}
/* 弹出框 */
.transition_page .alert_content_record{
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: 999999;
    background-color: rgba(0, 0, 0, 0.5);
    display: none;
}
.transition_page .alert_content{
    width: 80%;
    height: 100%;
    background-color: #fff;
    position: absolute;
    top: 0;
    right: 0rem;
    padding: 0.75rem;
    font-size: 0.8rem;
    overflow: scroll;
}
.transition_page .input_money{
    width: 100%;
    padding: 0.3rem;
    border: 1px solid #dedede;
    margin-top: 0.4rem;
    border-radius: 0.2rem;
}
.transition_page .alert_content>div{
    margin-top: 1.5rem;
}
.transition_page .alert_content>div:nth-of-type(1){
    margin-top: 0;
}
.transition_page .btn_group{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    /* justify-content: space-between; */
}
.transition_page .choose_ele_content div{
    color: #333333;
    margin-top: 0.5rem;
    height: 2rem;
    line-height: 1rem;
    width: calc(100% / 3 - 5px);
    text-align: center;
    border-radius: 0.1rem;
    background-color: #ebebeb;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    margin-left: 5px;
}
.transition_page .fun_btn_group div{
    width: 50%;
    padding: 0.75rem 0 !important;
    font-size: 0.9rem;
    text-align: center;
    background-color: #ebebeb;
    border-radius: 0.3rem;
    color: #333333;
    margin-top: 0.5rem;
}
.transition_page .fun_btn_group div:nth-of-type(1){
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
}
.transition_page .fun_btn_group div:nth-of-type(2){
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
}
.transition_page .fun_btn_group_active{
    color: #fff !important;
    background-color: #ff1daa !important;
}
.transition_page .choose_active{
    background-color: #fed6ec !important;
    color: #ff0192 !important;
    position: relative;
    /* background-image: url('../image/transition/close_icon.png');
    background-size: cover;
    background-size: 50% 50%;
    background-position: 100% 100%;
    background-repeat: no-repeat; */
}
.transition_page .choose_active img{
    position: absolute;
    right: 0;
    bottom: 0;
    height: 50%;
}
.transition_page .down_icon{
    height: 0.7rem;
    margin-left: 0.3rem;
}


