
.tranistion_record_detail_content{
    background-image: url('../../image/transition/transition_bg.png');
    background-size: 100% 100%;
}
.tranistion_record_detail .font-size-14{
    font-size:0.8rem !important;
}
.tranistion_record_detail .font-size-16{
    font-size: 1rem !important;
}
.tranistion_record_detail .text-color-pink{
    color: #fe028a;
}
.tranistion_record_detail .deep_blue{
    color: #00405f !important;
}
.tranistion_record_detail .transition_record_page_header{
    height: 3rem;
    position: relative;
    padding: 0 0.75rem;
    background-color: #070e1b;
}
.tranistion_record_detail .header_tab{
    width: 6rem;
    text-align: center;
    -ms-flex-pack: center;
        justify-content: center;
    position: absolute;
    left: calc(50% - 3rem);
    height: 60%;
    color: #c8c8c5;
    font-size: 1rem;
}
.tranistion_record_detail .list_ele{
    padding: 0.75rem 0.5rem;
    color: #d6d6d6;
    background-color: rgba(18, 32, 49, 0.5);
    font-size: 1rem;
    border-bottom: 1px dashed #001d30;
}

.tranistion_record_detail .create_time{
    padding: 0.5rem 0;
}
.tranistion_record_detail .back_icon{
    height: 1rem;
}
.tranistion_record_detail .transition_record_page_content{
    padding: 0.75rem;
    color: #ffffff;
}
.tranistion_record_detail .text-color-gray{
    color: #e5e4e4;
}
.tranistion_record_detail .order_status{
    font-size: 1.8rem;
    padding-top: 2rem;
    text-align: left;
}
.tranistion_record_detail .call_target{
    font-size: 1rem;
    margin-top: 0.3rem;
}
.tranistion_record_detail .list_title{
    padding: 1rem 0.75rem;
    margin-top: 1rem;
    /* background-color: rgba(49, 67, 77,0.5); */
    background-color: red;
    font-size: 1rem;
    border-top-right-radius: 0.3rem;
    border-top-left-radius: 0.3rem;
    border-bottom: 1px dashed #000202;
    position: relative;
    background: -webkit-radial-gradient(top left, circle, transparent 0, rgba(49, 67, 77,0.5) 0) top left, 
    -webkit-radial-gradient(top right, circle, transparent 0, rgba(49, 67, 77,0.5) 0) top right, 
    -webkit-radial-gradient(bottom right, circle, transparent 0.5rem, rgba(49, 67, 77,0.5) 0) bottom right, 
    -webkit-radial-gradient(bottom left, circle, transparent 0.5rem, rgba(49, 67, 77,0.5) 0) bottom left;
    background: -o-radial-gradient(top left, circle, transparent 0, rgba(49, 67, 77,0.5) 0) top left, 
    -o-radial-gradient(top right, circle, transparent 0, rgba(49, 67, 77,0.5) 0) top right, 
    -o-radial-gradient(bottom right, circle, transparent 0.5rem, rgba(49, 67, 77,0.5) 0) bottom right, 
    -o-radial-gradient(bottom left, circle, transparent 0.5rem, rgba(49, 67, 77,0.5) 0) bottom left;
    background: radial-gradient(circle at top left, transparent 0, rgba(49, 67, 77,0.5) 0) top left, 
    radial-gradient(circle at top right, transparent 0, rgba(49, 67, 77,0.5) 0) top right, 
    radial-gradient(circle at bottom right, transparent 0.5rem, rgba(49, 67, 77,0.5) 0) bottom right, 
    radial-gradient(circle at bottom left, transparent 0.5rem, rgba(49, 67, 77,0.5) 0) bottom left;
    background-size: 50.15% 51%;
    background-repeat: no-repeat;
}
.tranistion_record_detail .list_title div{
    text-align: left;
}
.tranistion_record_detail .list_ele:nth-of-type(1){
    background: -webkit-radial-gradient(top left, circle, transparent 0.5rem, rgba(18, 32, 49, 0.5) 0) top left, 
    -webkit-radial-gradient(top right, circle, transparent 0.5rem, rgba(18, 32, 49, 0.5) 0) top right, 
    -webkit-radial-gradient(bottom right, circle, transparent 0, rgba(18, 32, 49, 0.5) 0) bottom right, 
    -webkit-radial-gradient(bottom left, circle, transparent 0, rgba(18, 32, 49, 0.5) 0) bottom left;
    background: -o-radial-gradient(top left, circle, transparent 0.5rem, rgba(18, 32, 49, 0.5) 0) top left, 
    -o-radial-gradient(top right, circle, transparent 0.5rem, rgba(18, 32, 49, 0.5) 0) top right, 
    -o-radial-gradient(bottom right, circle, transparent 0, rgba(18, 32, 49, 0.5) 0) bottom right, 
    -o-radial-gradient(bottom left, circle, transparent 0, rgba(18, 32, 49, 0.5) 0) bottom left;
    background: radial-gradient(circle at top left, transparent 0.5rem, rgba(18, 32, 49, 0.5) 0) top left, 
    radial-gradient(circle at top right, transparent 0.5rem, rgba(18, 32, 49, 0.5) 0) top right, 
    radial-gradient(circle at bottom right, transparent 0, rgba(18, 32, 49, 0.5) 0) bottom right, 
    radial-gradient(circle at bottom left, transparent 0, rgba(18, 32, 49, 0.5) 0) bottom left;
    background-size: 50.15% 51%;
    background-repeat: no-repeat;
}
.tranistion_record_detail .price{
    font-size: 1.5rem;
    color: #00d9ff;
    font-weight: bold;
}
.tranistion_record_detail .auth_icon{
    height: 1rem;
    margin-left: 0.5rem;
}
.tranistion_record_detail .default_auth_icon{
    width: 100%;
    margin: 1rem auto;
}
.tranistion_record_detail .submit_btn{
    padding: 0.75rem;
    width: 100%;
    text-align: center;
    color: #ffffff;
    margin-top: 2rem;
    border-radius: 0.3rem;
    font-size: 1rem;
}
.tranistion_record_detail .submit_btn:nth-last-of-type(1){
    margin-top: 0;
}
.tranistion_record_detail .upload_img{
    opacity: 0;
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: -10;
}
.tranistion_record_detail .qrcode_img{
    height: 5rem;
    width: 5rem;
    min-height: 5rem;
    min-width: 5rem;
}


