.tip_lists_page .tip_lists_content{
    background-image: url('../../image/transition/transition_bg.png');
    background-size: 100% 100%;
}
.tip_lists_page .font-size-14{
    font-size:0.8rem !important;
}
.tip_lists_page .font-size-16{
    font-size: 1rem !important;
}
.tip_lists_page .text-color-pink{
    color: #fe028a;
}
.tip_lists_page .deep_blue{
    color: #00405f !important;
}
.tip_lists_page .sky_blue{
    color: #09a9c6 !important;
}
.tip_lists_page .tip_lists_page_header{
    height: 3rem;
    position: relative;
    padding: 0 0.75rem;
    background-color: #070e1b;
}
.tip_lists_page .header_tab{
    width: 6rem;
    text-align: center;
    -ms-flex-pack: center;
        justify-content: center;
    position: absolute;
    left: calc(50% - 3rem);
    height: 60%;
    color: #c8c8c5;
    font-size: 1rem;
}
.tip_lists_page .list_content{
    padding-bottom: 1rem;
    margin-top: 1rem;
}
.tip_lists_page .list_ele{
    padding: 0.75rem 1.5rem;
    color: #d6d6d6;
    width: 90%;
    background-color: rgba(206, 235, 255, 0.15);
    /* margin-top: 1rem; */
    margin: 1rem auto 0;
    border-radius: 0.4rem;
}
.tip_lists_page .list_ele:nth-of-type(1){
    margin-top: 0;
}
.tip_lists_page .record_icon{
    height: 1.5rem;
}
.tip_lists_page .list_ele_title{
    padding: 0.5rem 0;
    border-bottom: 1px dashed #5b79ae;
}
.tip_lists_page .create_time{
    padding: 0.5rem 0;
    text-align: left;
}

/* 弹出框 */
.tip_lists_page .alert_content_record{
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: 99;
    background-color: rgba(0, 0, 0, 0.5);
    display: none;
}
.tip_lists_page .alert_content{
    width: 80%;
    /* height: 50%; */
    background-color: #fff;
    position: absolute;
    top: 3rem;
    right: 0.75rem;
    border-radius: 0.5rem;
    padding: 0.75rem;
    font-size: 0.8rem;
}
.tip_lists_page .alert_content>div{
    margin-top: 1rem;
}
.tip_lists_page .alert_content>div:nth-of-type(1){
    margin-top: 0;
}
.choose_ele_content{
    text-align: left;
}
.tip_lists_page .btn_group span{
    color: #333333;
    display: inline-block;
    margin-top: 0.5rem;
    padding: 0.5rem 0rem;
    width: calc(100% / 3 - 5px);
    text-align: center;
    border-radius: 0.3rem;
    margin-left: .25rem;
    background-color: #ebebeb;
}
.tip_lists_page .fun_btn_group span{
    width: 50%;
    padding: 0.7rem 0;
    font-size: 0.9rem;
}
.tip_lists_page .fun_btn_group span:nth-of-type(1){
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
}
.tip_lists_page .fun_btn_group span:nth-of-type(2){
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
}
.tip_lists_page .fun_btn_group_active{
    color: #fff !important;
    background-color: #ff1daa !important;
}
.tip_lists_page .choose_active{
    background-color: #fed6ec !important;
    color: #ff0192 !important;
    position: relative;
}
.tip_lists_page .choose_active img{
    position: absolute;
    right: 0;
    bottom: 0;
    height: 50%;
}
.tip_lists_page .list_ele_msg_content{
    padding-top: 0.75rem;
}
.tip_lists_page .default_icon{
    width: 3rem;
    min-width: 2rem;
}
.tip_lists_page .list_ele_msg_content_label{
    font-size: 0.7rem;
    /* text-align: center; */
}
.tip_lists_page .list_ele_account,.tip_lists_page .list_ele_address{
    width: 50%;
    overflow: hidden;
    margin-left: 0.5rem;
}
.tip_lists_page .list_ele_account p{
    width: 100%;
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    white-space: nowrap;
    /* text-align: center; */
}
.tip_lists_page .list_ele_address p{
    width: 100%;
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    padding: 0 0.5rem;
}

.tip_lists_page .tip_lists_page_content_header_content{
    padding: 0.2rem;
    background-color: rgba(206, 235, 255,0.15)
}
.tip_lists_page .tip_lists_page_content_header{
    font-size: 1rem;
    padding: 0.75rem 0;
    width: 100%;
    position: relative;
    display: -ms-flexbox;
    display: flex;
}
.tip_lists_page .tip_lists_page_content_header::after{
    content: '';
    height: 3px;
    width: 90%;
    background-color: #000000;
    position: absolute;
    left: 5%;
    bottom: 0;
    border-radius: 3px !important;
}
.tip_lists_page .tip_lists_page_content_header div{
    position: relative;
    height: 100%;
    width: calc(100% / 3);
    text-align: center;
    color: #00a1bb;
}
.tip_lists_page .tip_lists_page_content_header div span{
    position:relative;
}
.tip_lists_page .tip_lists_page_content_header  div span::before{
    content: '';
    height: 2px;
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 0;
}
.tip_lists_page .tip_lists_page_content_header_active{
    /* position: relative; */
    color: #00ffef !important;
}
.tip_lists_page .tip_lists_page_content_header_active::before{
    background-image: url('../../image/header_active.png');
    background-size: 100% 100%;
    z-index: 9;
    bottom: calc(-0.75rem - 10px) !important;
    height: 10px !important;
}


