.notice_page .notice_page_content{
    background-image: url('../../image/mine/mine_bg.png');
    background-size: 100% 100%;
    color: #ededed;
    background-attachment: fixed;
    font-size: 0.9rem;
}
.notice_page .notice_page_content{
    padding: 1rem;
}
.notice_page .list_ele{
    border-radius: 0.5rem;
    padding: 0.5rem;
    background-color: rgba(218, 246, 255, 0.15);
    margin-top: 0.75rem;
}
.notice_page .list_ele_timer_bar{
    padding: 0.5rem;
    border-bottom: 1px dashed #5978af;
}
.notice_page .list_ele_content{
    padding: 0.5rem;
    border-bottom: 1px dashed #5978af;
}
.notice_page .list_ele_title{
    font-size: 1.2rem;
    color: #00f6ff;
    padding: 0.3rem 0;
}
.notice_page .list_ele_read_num{
    padding: 0.5rem;
}


