.new_player_component .new_player{
    padding: 1rem 0.5rem;
}
.new_player_component .new_player .list-title , .new_player_component .new_player .list-item{
    content: "";
    clear: both;
    display: table;
    width: 100%;
}
.new_player_component .new_player .list-title div , .new_player_component .new_player .list-item div{
    padding:0.5rem 0;
    width: calc( 100% / 3);
    text-align: center;
    float: left;
}
.new_player_component .list-title div:nth-of-type(1) ,.new_player_component .list-item div:nth-of-type(1){
    text-align: left;
}
.new_player_component .list-title div:nth-of-type(3) ,.new_player_component .list-item div:nth-of-type(3){
    text-align: right;
}
.new_player_component .new_player .list-item div{
    color: #00bdd2;
    padding:0.25rem 0;
}


