.add_change_language_page .add_change_language_content{
    padding: 1rem;
    background-image: url('../../image/mine/mine_bg.png');
    background-size: 100% 100%;
    color: #ffffff;
    background-attachment: fixed;
}
.add_change_language_page .add_change_language_main{
    padding-top: 2rem;
}
.add_change_language_page .list_ele{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    margin-top: 0.1rem;
    padding: 0.75rem;
    background-color: rgba(206, 235, 255,0.15);
    border-radius: 0.3rem;
}
.add_change_language_page .list_ele label{
    font-size: 1.1rem;
}
.add_change_language_page .submit_btn{
    padding: 0.75rem;
    width: 100%;
    text-align: center;
    color: #ffffff;
    margin-top: 2rem;
    border-radius: 0.3rem;
    font-size: 1rem;
}
.add_change_language_page .radio:checked{
    background-color: #ff1cab;
    border: solid 1px #ff1cab;
    text-align: center;
    background-clip: padding-box;
}
.add_change_language_page .radio {
    width: 1.5rem !important;
    min-width: 1.5rem !important;
    height: 1.5rem;
    background-color: #ffffff;
    border: solid 1px #dddddd;
    border-radius: 0.75rem;
    font-size: 0.8rem;
    margin: 0;
    padding: 0;
    position: relative;
    display: inline-block;
    vertical-align: top;
    cursor: default;
    -webkit-appearance: none;
    -webkit-user-select: none;
    -moz-user-select: none;
     -ms-user-select: none;
         user-select: none;
    -webkit-transition: background-color ease 0.1s;
    -o-transition: background-color ease 0.1s;
    transition: background-color ease 0.1s;
}
.add_change_language_page .radio:checked:after{
    content: '';
    width: 0.5rem;
    height: 0.3rem;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -0.25rem;
    margin-top: -0.25rem;
    background: transparent;
    border: 1px solid #ffffff;
    border-top: none;
    border-right: none;
    z-index: 2;
    border-radius: 0;
    -webkit-transform: rotate(-45deg);
    -ms-transform: rotate(-45deg);
        transform: rotate(-45deg);
}


