.islands_component .group_content_1,.group_content_2{
    border: 1px solid #00c0ff;
    border-top: none;
    -webkit-box-shadow: 0 0 2rem #00c0ff inset;
            box-shadow: 0 0 2rem #00c0ff inset;
    margin-top: 3rem;
    padding: 0 0.5rem;
    padding-bottom: 1.5rem;
    position: relative;
}
/* 四角定位 */
.islands_component .four_corner div{
    position: absolute;
    height: 2rem;
    width: 2rem;
    border-left: 3px solid #00d2ff;
    border-bottom: 3px solid #00d2ff;
}
.islands_component .left_top{
    top: -1.5px;
    left: -2px;
    -webkit-transform: rotate(90deg);
        -ms-transform: rotate(90deg);
            transform: rotate(90deg);
}
.islands_component .left_bottom{
    bottom: -1.5px;
    left: -2px;
}
.islands_component .right_top{
    top: -1.5px;
    right: -2px;
    -webkit-transform: rotate(180deg);
        -ms-transform: rotate(180deg);
            transform: rotate(180deg);
}
.islands_component .right_bottom{
    bottom: -1.5px;
    right: -2px;
    -webkit-transform: rotate(270deg);
        -ms-transform: rotate(270deg);
            transform: rotate(270deg);
}
.islands_component .group_content_1_btn_group,.group_content_2_btn_group{
    position: absolute;
    top: -1.5rem;
    left: 0;
    width: 100%;
    height: 3rem;
    padding: 0 2rem;
    z-index: 9;
}
.islands_component .group_content_1_btn_group div,.group_content_2_btn_group div{
    font-size: 0.8rem;
    background-color: transparent;
    padding: 0 0.75rem;
    color: #00c0ff;
}
/* 阴影 */
.islands_component .group_content_1_btn_group_active, .islands_component .group_content_2_btn_group_active{
    position: relative;
    font-size: 0.9rem !important;
    color: #ffffff !important;
}
.islands_component .group_content_1_btn_group_active::before, .islands_component .group_content_2_btn_group_active::before{
    content: '[';
    height: 100%;
    width: 1rem;
    position: absolute;
    font-size: 1.3rem;
    left: 0;
    color: #00c0ff;
    top: -38%;
    /* background-color: red; */
}
.islands_component .group_content_1_btn_group_active::after, .islands_component .group_content_2_btn_group_active::after{
    content: ']';
    height: 100%;
    width: 1rem;
    position: absolute;
    font-size: 1.3rem;
    right: 0;
    top: -38%;
    color: #00c0ff;
    text-align: right;
}
.islands_component .text-color-blue{
    color: #00c0ff;
}

.islands_component .group_content_2{
    margin-bottom: 2rem;
}
.islands_component .group_content_2_btn_group div{
    font-size: 0.8rem;
    padding: 0.3rem;
}
.islands_component .group_content_2_btn_group_active{
    padding: 0.5rem !important;
}
.islands_component .group_content_2_btn_group_active::before{
    top: 0%;
}
.islands_component .group_content_2_btn_group_active::after{
    top: 0%;
}


