.buy_token_com .waring_bar{
    padding: 1.5rem 0.75rem 1rem;
}
.buy_token_com .buy_input_content{
    padding: 0 0.75rem;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.buy_token_com .key_content{
    /* padding: 0.3rem 0; */
    height: 3rem;
    min-width: 2.5rem;
    text-align: center;
    background-image: -webkit-linear-gradient(0deg, #00b6fa, #3c68fd);
}
.buy_token_com .key_icon{
    height: 2rem;
}
.buy_token_com .buy_list_key_icon{
    height: 1rem;
    margin-left:0.2rem;
}
.buy_token_com .buy_input_input{
    height: 3rem;
    background-color: #ffffff;
    border: none;
    color: #ff0674;
    text-align: center;
    font-size: 1rem;
    border-radius: 0;
}
.buy_token_com .buy_input_unit{
    height: 3rem;
    padding: 0 0.5rem;
    max-width: 50%;
    min-width: 50%;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    background-image: -webkit-linear-gradient(0deg, #06acf9, #5543fa, #f859b0);
}
.buy_token_com .buy_list_content{
    padding: 0 0.75rem;
    margin: 1rem 0;
}
.buy_token_com .buy_list_content div{
    display: -ms-flexbox;
    display: flex;
    height: 2.5rem;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    width: calc(100% / 5);
    text-align: center;
    margin-left: .05rem;
    background-color: #ffffff;
}
.buy_token_com .buy_list_content div span{
    color: #ff0a76;
}
.buy_token_com .buy_list_content_active{
    background-image: -webkit-linear-gradient(0deg, #00b6fa, #3c68fd);
}
.buy_token_com .buy_list_content_active span{
    color: #ffffff !important;
}
/* 选择战队 */
.buy_token_com .choose_team_content{
    padding: 0 0.75rem;
}
.buy_token_com .team_content{
    margin: 1rem 0;
    border: 1px solid #0cc1d8;
}
.buy_token_com .team_content li{
    border-right: 1px solid #0cc1d8;
    padding: 1rem 0;
}
.buy_token_com .team_content li p:nth-of-type(1){
    margin: 0.5rem;
    font-size: 1rem;
}
.buy_token_com .team_content li p{
    color: #0cc1d8;
}
.buy_token_com .team_content li:nth-of-type(1) p{
    color: #ffffff;
}
.buy_token_com .team_description{
    color: #0cd8fc;
}
.buy_token_com .choose_team_question_icon{
    height: 1rem;
    margin-left: 0.3rem;
}
.buy_token_com .team_icon{
    width: 80%;
}
.buy_token_com .choose_team_buy_btn{
    width: 100%;
    background-image: -webkit-linear-gradient(0deg, rgba(8,175,251,0.8),rgba(94,67,252,0.8), rgba(249,93,184,0.8));
    text-align: center;
    font-size: 1rem;
    padding: 0.75rem 0;
    border-radius: 0.5rem;
    margin-top: 1rem;
    color:#00b6fa;
}


