.notice_detail_page .notice_detail_content{
    background-image: url('../../image/mine/mine_bg.png');
    background-size: 100% 100%;
    color: #ededed;
    background-attachment: fixed;
    font-size: 0.9rem;
    padding: 1rem;
}
.notice_detail_page .notice_detail_main{
    padding: 0.5rem;
    margin-top: 1rem;
    background-color: rgba(218, 246, 255, 0.15);
    border-radius: 0.5rem;
}
.notice_detail_page .notice_detail_main_title{
    font-size: 1.2rem;
    color: #00f6ff;
    text-align: center;
}
.notice_detail_page .notice_detail_main_time{
    font-size: 0.8rem;
    text-align: center;
    color: #d0cece;
    margin: 0.5rem 0;
}
.notice_detail_page .notice_detail_main_content{
    text-indent: 2rem;
    color: #ededed;
    padding: 0.5rem;
}



