.team_count_component{
    padding:1rem 0;
}
.team_count_component .pool_text_color{
    color: #fff;
    font-size: 1.3rem;
    text-shadow: 0 0 18px #ff0473,0 0px 18px #ffffff;
}
.team_count_component .pool_list_ele{
    margin-top: 1rem;
}
.team_count_component .pool_list_ele p{
    font-size: 0.7rem;
    color: #00c9f3;
    text-align: right;
    padding-right: 1.5rem;
    margin-top: 0.3rem;
    white-space: nowrap;
}
.team_count_component .team_descript{
    text-align: left !important;
}
.team_count_component .pool_eth_icon{
    height: 1.5rem;
}
.team_count_component .pool_label{
    font-size: 1rem;
    color: #00c9f3;
    min-width: 5rem;
    max-width: 5rem;
    overflow: hidden;
    white-space: nowrap;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}
.team_count_component .last-l{
    border-bottom: 1px solid #156086;
    padding-bottom: 1rem;
}
.team_count_component .get-wallet{
    width: 100%;
    background-image: -webkit-linear-gradient(0deg, rgba(8,175,251,0.8),rgba(94,67,252,0.8), rgba(249,93,184,0.8));
    text-align: center;
    font-size: 1rem;
    padding: 0.75rem 0;
    border-radius: 0.5rem;
    color: #16f7ff;
}
.team_count_component .title{
    font-size: 1.1rem;
    text-align: center;
    padding-top: 2rem;
    border-bottom: 1px solid #156086;
    padding-bottom: 1.5rem;
}
.team_count_component .left-img{
    width: 3rem;;
}
.team_count_component .right-wrap{
    width: 100%;
    padding-left: 0.5rem;
}
.team_count_component .team_count_total_amount{
    display: inline-block;
    min-width: 9rem;
    max-width: 9rem;
    overflow: hidden;
    white-space: nowrap;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}


